"use client";

import React, { useState } from 'react';
import Link from 'next/link';
import styles from './Navbar.module.css';
import { Button } from '../ui/Button';

export const Navbar = () => {
    const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

    const toggleMenu = () => {
        setIsMobileMenuOpen(!isMobileMenuOpen);
    };

    return (
        <>
            <nav className={styles.navbar}>
                <Link href="/" className={styles.logo}>
                    StudentPortal
                </Link>

                <div className={styles.navLinks}>
                    <Link href="/opportunities" className={styles.link}>Opportunities</Link>
                    <Link href="/dashboard" className={styles.link}>Dashboard</Link>
                    <Link href="/login">
                        <Button variant="filled">Login</Button>
                    </Link>
                </div>

                <button className={styles.mobileMenuBtn} onClick={toggleMenu} aria-label="Toggle menu">
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M3 12H21" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                        <path d="M3 6H21" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                        <path d="M3 18H21" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
                    </svg>
                </button>
            </nav>

            <div className={`${styles.mobileMenu} ${isMobileMenuOpen ? styles.open : ''}`}>
                <Link href="/opportunities" className={styles.link} onClick={toggleMenu}>Opportunities</Link>
                <Link href="/dashboard" className={styles.link} onClick={toggleMenu}>Dashboard</Link>
                <Link href="/login" onClick={toggleMenu}>
                    <Button variant="filled" style={{ width: '100%' }}>Login</Button>
                </Link>
            </div>
        </>
    );
};
